import { Component, Input, OnInit } from '@angular/core';
import { AdminServiceService } from '../admin-service.service';
import { Employee } from '../model/employee';
import { ToastrService  } from 'ngx-toastr';

@Component({
  selector: 'app-update-employee',
  templateUrl: './update-employee.component.html',
  styleUrls: ['./update-employee.component.css']
})
export class UpdateEmployeeComponent implements OnInit {

  @Input() parentdata=<Employee>{};

  allCountry:any[]=[];

  issubmitDissabled:boolean=true;
  backendResponse:string='';
  constructor(private service:AdminServiceService,private toaster : ToastrService ) { }

  ngOnInit() {
    this.GetAllCountry();
  }

  GetAllCountry(){
    this.service.getAllCountries()
    .subscribe((response)=>{
      this.allCountry=(<any>response);
    })
  }


  onupdate(){
    this.parentdata.updatedDate=Date.now();
    this.parentdata.updatedBy=sessionStorage.getItem("employeeName");
    this.service.UpdateEmployee(this.parentdata)
    .subscribe((response)=>{
      console.log(response);
      this.issubmitDissabled=false;
      this.backendResponse=response;
      this.toaster.success(response);
    })
  }

}
