import { Component, OnInit, TemplateRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { AdminServiceService } from '../admin-service.service';
import { Employee } from '../model/employee';

@Component({
  selector: 'app-employeedetails',
  templateUrl: './employeedetails.component.html',
  styleUrls: ['./employeedetails.component.css']
})
export class EmployeedetailsComponent implements OnInit {
  

  constructor(private route : ActivatedRoute,
     private service : AdminServiceService,
     private modalservice:BsModalService) { }
  // myid : number;
  
  empobj = <Employee>{};
  modalRef: BsModalRef;  

  config = {
    animated: true,
    
    ignoreBackdropClick: true,
    class: "alert alert-danger"
  };


  ngOnInit() {
    this.GetDataFromUrl();
  }

  GetDataFromUrl(){
   this.route.paramMap
   .subscribe((param)=>{
    //this.myid =+ param.get("adminid");
    //console.log(this.myid);
    this.GetDataFromBackend(param.get("employeeId"));
   })
  }

  GetDataFromBackend(employeeId){
   this.service.getEmployeeById(employeeId)
   .subscribe((response)=>{
    console.log(response);
    this.empobj = (<Employee>response);
   })

  }

  OnUpdate(popup : TemplateRef<any>){
    console.log(this.empobj)
    this.modalRef = this.modalservice.show(popup, this.config);
  }
}
